@font-face {
    font-family: "Ionicons";
    src: url("//cdnjs.cloudflare.com/ajax/libs/ionicons/2.0.1/fonts/ionicons.eot?v=2.0.0");
    src: url("//cdnjs.cloudflare.com/ajax/libs/ionicons/2.0.1/fonts/ionicons.eot?v=2.0.0#iefix") format("embedded-opentype"), url("//cdnjs.cloudflare.com/ajax/libs/ionicons/2.0.1/fonts/ionicons.ttf?v=2.0.0") format("truetype"), url("//cdnjs.cloudflare.com/ajax/libs/ionicons/2.0.1/fonts/ionicons.woff?v=2.0.0") format("woff"), url("//cdnjs.cloudflare.com/ajax/libs/ionicons/2.0.1/fonts/ionicons.svg?v=2.0.0#Ionicons") format("svg");
    font-weight: normal;
    font-style: normal;
}

.image-gallery-fullscreen-button::before,
.image-gallery-play-button::before,
.image-gallery-left-nav::before,
.image-gallery-right-nav::before {
    display: inline-block;
    font-family: "Ionicons";
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    text-rendering: auto;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.image-gallery {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}

.image-gallery.fullscreen-modal {
    background: #000;
    bottom: 0;
    height: 100%;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    width: 100%;
    z-index: 5;
}

.image-gallery.fullscreen-modal .image-gallery-content {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.image-gallery-content {
    top: 0;
}

.image-gallery-content.fullscreen {
    background: #000;
}

.image-gallery-slide-wrapper {
    position: relative;
}

.image-gallery-fullscreen-button,
.image-gallery-play-button,
.image-gallery-left-nav,
.image-gallery-right-nav {
    cursor: pointer;
    position: absolute;
    z-index: 4;
}

.image-gallery-fullscreen-button::before,
.image-gallery-play-button::before,
.image-gallery-left-nav::before,
.image-gallery-right-nav::before {
    color: #fff;
    line-height: .7;
    text-shadow: 0 2px 2px #1a1a1a;
    transition: color .2s ease-out;
}

.image-gallery-fullscreen-button:hover::before,
.image-gallery-play-button:hover::before,
.image-gallery-left-nav:hover::before,
.image-gallery-right-nav:hover::before {
    color: #337ab7;
}

@media (max-width: 768px) {
    .image-gallery-fullscreen-button:hover::before,
    .image-gallery-play-button:hover::before,
    .image-gallery-left-nav:hover::before,
    .image-gallery-right-nav:hover::before {
        color: #fff;
    }
}

.image-gallery-fullscreen-button,
.image-gallery-play-button {
    bottom: 0;
}

.image-gallery-fullscreen-button::before,
.image-gallery-play-button::before {
    font-size: 1.7em;
    padding: 15px 20px;
    text-shadow: 0 1px 1px #1a1a1a;
}

@media (max-width: 768px) {
    .image-gallery-fullscreen-button::before,
    .image-gallery-play-button::before {
        font-size: 1.4em;
    }
}

@media (max-width: 480px) {
    .image-gallery-fullscreen-button::before,
    .image-gallery-play-button::before {
        font-size: 1em;
    }
}

.image-gallery-fullscreen-button:hover::before,
.image-gallery-play-button:hover::before {
    color: #fff;
    transform: scale(1.1);
}

@media (max-width: 768px) {
    .image-gallery-fullscreen-button:hover::before,
    .image-gallery-play-button:hover::before {
        transform: none;
    }
}

.image-gallery-fullscreen-button {
    right: 0;
}

.image-gallery-fullscreen-button::before {
    content: "";
}

.image-gallery-fullscreen-button.active::before {
    content: "";
}

.image-gallery-fullscreen-button.active:hover::before {
    transform: scale(0.9);
}

.image-gallery-play-button {
    left: 0;
}

.image-gallery-play-button::before {
    content: "";
}

.image-gallery-play-button.active::before {
    content: "";
}

.image-gallery-left-nav,
.image-gallery-right-nav {
    appearance: none;
    background-color: transparent;
    border: 0;
    color: #fff;
    font-size: 5em;
    outline: none;
    padding: 50px 15px;
    top: 50%;
    transform: translateY(-50%);
}

.image-gallery-left-nav[disabled],
.image-gallery-right-nav[disabled] {
    cursor: disabled;
    opacity: .6;
    pointer-events: none;
}

@media (max-width: 768px) {
    .image-gallery-left-nav,
    .image-gallery-right-nav {
        font-size: 3.4em;
    }
}

@media (max-width: 480px) {
    .image-gallery-left-nav,
    .image-gallery-right-nav {
        font-size: 2.4em;
    }
}

.image-gallery-left-nav {
    left: 0;
}

.image-gallery-left-nav::before {
    content: "";
}

.image-gallery-right-nav {
    right: 0;
}

.image-gallery-right-nav::before {
    content: "";
}

.image-gallery-slides {
    line-height: 0;
    overflow: hidden;
    position: relative;
    white-space: nowrap;
}

.image-gallery-slide {
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}

.image-gallery-slide.center {
    position: relative;
}

.image-gallery-slide img {
    width: 100%;
}

.image-gallery-slide .image-gallery-description {
    background: rgba(0, 0, 0, 0.4);
    bottom: 70px;
    color: #fff;
    left: 0;
    line-height: 1;
    padding: 10px 20px;
    position: absolute;
    white-space: normal;
}

@media (max-width: 768px) {
    .image-gallery-slide .image-gallery-description {
        bottom: 45px;
        font-size: .8em;
        padding: 8px 15px;
    }
}

.image-gallery-bullets {
    bottom: 20px;
    left: 0;
    margin: 0 auto;
    position: absolute;
    right: 0;
    width: 80%;
    z-index: 4;
}

.image-gallery-bullets .image-gallery-bullets-container {
    margin: 0;
    padding: 0;
    text-align: center;
}

.image-gallery-bullets .image-gallery-bullet {
    appearance: none;
    background-color: transparent;
    border: 1px solid #fff;
    border-radius: 50%;
    box-shadow: 0 1px 0 #1a1a1a;
    cursor: pointer;
    display: inline-block;
    margin: 0 5px;
    outline: none;
    padding: 5px;
}

@media (max-width: 768px) {
    .image-gallery-bullets .image-gallery-bullet {
        margin: 0 3px;
        padding: 3px;
    }
}

@media (max-width: 480px) {
    .image-gallery-bullets .image-gallery-bullet {
        padding: 2.7px;
    }
}

.image-gallery-bullets .image-gallery-bullet.active {
    background: #fff;
}

.image-gallery-thumbnails {
    overflow: hidden;
    padding: 5px 0;
}

.image-gallery-thumbnails .image-gallery-thumbnails-container {
    cursor: pointer;
    text-align: center;
    transition: transform .45s ease-out;
    white-space: nowrap;
}

.image-gallery-thumbnail {
    display: inline-block;
    padding-right: 5px;
}

.image-gallery-thumbnail img {
    border: 4px solid transparent;
    transition: border .3s ease-out;
    vertical-align: middle;
    width: 100px;
}

@media (max-width: 768px) {
    .image-gallery-thumbnail img {
        border: 3px solid transparent;
        width: 75px;
    }
}

.image-gallery-thumbnail.active img {
    border: 3px solid #337ab7;
}

@media (max-width: 768px) {
    .image-gallery-thumbnail.active img {
        border: 3px solid #337ab7;
    }
}

.image-gallery-thumbnail-label {
    color: #1a1a1a;
    font-size: 1em;
}

@media (max-width: 768px) {
    .image-gallery-thumbnail-label {
        font-size: .8em;
    }
}

.image-gallery-index {
    background: rgba(0, 0, 0, 0.4);
    color: #fff;
    line-height: 1;
    padding: 10px 20px;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 4;
}

@media (max-width: 768px) {
    .image-gallery-index {
        font-size: .8em;
        padding: 5px 10px;
    }
}

.image-gallery-thumbnail.active{
    padding-right: 0px;
}